.site-header__button {
  flex: 1;
}
@media (min-width: 1200px) {
  .site-header__button {
    display: none;
  }
}

.site-header {
  position: relative;
  display: flex;
  align-items: center;
  padding: var(--spacing-lg);
  background-color: var(--surface-action-1);
  color: var(--text-on-action);
}
@media (min-width: 1200px) {
  .site-header {
    position: unset;
    display: grid;
    grid-template-columns: 166px auto;
    grid-template-rows: 1fr 1fr;
    align-items: stretch;
    height: 126px;
    padding: unset;
    background-color: unset;
    color: var(--text-action);
  }
}

.site-header__logo {
  display: flex;
  justify-content: center;
  align-items: center;
  flex: 1;
}
@media (min-width: 1200px) {
  .site-header__logo {
    grid-column: 1;
    grid-row: span 2;
    flex: auto;
  }
}

.site-header__top {
  display: none;
}
@media (min-width: 1200px) {
  .site-header__top {
    display: flex;
    grid-column: 2;
    grid-row: 1;
    align-items: center;
    padding-inline: var(--spacing-lg);
    background-color: var(--surface-action-1);
    color: var(--text-on-action);
  }
}

.site-header__bottom {
  display: flex;
  align-items: center;
  justify-content: flex-end;
  flex: 1;
}
@media (min-width: 1200px) {
  .site-header__bottom {
    grid-column: 2;
    grid-row: 2;
    flex: unset;
    justify-content: unset;
    padding-inline-start: var(--spacing-lg);
  }
}

.site-header__secondary-menu-mobile {
  flex: 1;
}
@media (min-width: 1200px) {
  .site-header__secondary-menu-mobile {
    display: none;
  }
}

.site-header__site-name {
  margin-inline-end: auto;
  font-family: var(--font-serif);
  text-transform: uppercase;
  font-size: var(--sub-heading);
  letter-spacing: 1.44px;
}